import React from "react"
import { Link } from "gatsby"
import { StickyContainer, Sticky } from "react-sticky"
import Bio from "../components/bio"
import { rhythm, scale } from "../utils/typography"

const Layout = ({ location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`
  let header

  if (location.pathname === rootPath) {
    header = (
      <h1
        style={{
          ...scale(1.5),
          marginBottom: rhythm(1.5),
          marginTop: 0,
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h1>
    )
  } else {
    header = (
      <h3
        style={{
          fontFamily: `Montserrat, sans-serif`,
          marginTop: 0,
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h3>
    )
  }
  return (
    <div style={{boxFlex: '1', marginLeft: 'auto', marginRight: 'auto'}}>
      <div style={{display: 'flex', marginLeft: 'auto', marginRight: 'auto', justifyContent: 'center'}}>
      <StickyContainer
        className = "layout-aside-bio"
        style={{
          float: `left`,
          top: `0`,
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
          maxWidth: rhythm(12.85),
        }}>
        <Sticky>
          {({
            isSticky = `true`,
            wasSticky = `true`,
            calculatedHeight = `100vh - 2em - 2em`,
          }) => (
            <Bio />
          )}
        </Sticky>
      </StickyContainer>
      <div
        style={{
          maxWidth: rhythm(24),
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
          float: `right`,
        }}
      >
        <header>{header}</header>
        <main>{children}</main>
        <footer>
           pehcy {new Date().getFullYear()}, Built with
          {` `}
          <Link
            to = "https://www.gatsbyjs.org"
            style={{
              textDecoration: `none`,
              color: `inherit`,
            }}
          >
            Gatsby
          </Link>
        </footer>
      </div>
      </div>
    </div>
  )
}

export default Layout
